<template>
  <manufacturer-form
    @save-manufacturer="addManufacturer"
    :model="model">
  </manufacturer-form>
</template>

<script>
// import ManufacturerForm from '@/components/manufacturers/ManufacturerForm.vue';

export default {
  data() {
    return {
      model: {},
    };
  },
  methods: {
    addManufacturer(model) {
      this.$store.dispatch('addManufacturer', {
        manufacturer: model,
      });
    },
  },
  components: {
    'manufacturer-form': () => import('@/components/manufacturers/ManufacturerForm'),
  },
};
</script>
